<template>
  <div class="hs-container">
    <c-header :title="$t('base.ResetPassword')" isBackNav></c-header>
    <div class="title">
      <h1>{{$t('base.ThirdStep')}}</h1>
      <p>{{$t('base.PleaseEnterANewPassword')}}</p>
    </div>
    <div class="information">
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell">
          <div class="logo" style="background-image: url('static/image/login/password-.png')"></div>
          <input maxlength='50' v-model="NewPwd" :placeholder="$t('base.PleaseEnterANewPassword')"/>
        </div>
      </hs-cell-1>
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell">
          <div class="logo" style="background-image: url('static/image/login/password-.png')"></div>
          <input maxlength='50' v-model="NewPwdAgain" :placeholder="$t('base.PleaseConfirmTheNewPassword')"/>
        </div>
      </hs-cell-1>
    </div>
    <hs-gradient-button style="margin-top: 50px" :title="$t('base.Confirm')" @click="_nextStep"></hs-gradient-button>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import {hex_sha1} from 'common/js/sha1' // eslint-disable-line
import {kpiUserResetPassword} from 'api'
export default {
  computed: {
    details() {
      return JSON.parse(this.$route.query.details)
    }
  },
  data() {
    return {
      NewPwd: '',
      NewPwdAgain: ''
    }
  },
  methods: {
    _nextStep() {
      if (this.NewPwd !== this.NewPwdAgain) {
        this.$toast(this.$t('TM.InconsistentPasswordEnteredTwice'))
        return
      } else if (this.NewPwd.length < 6) {
        this.$toast(this.$t('TM.PasswordMustBeNoLessThanSixDigitsInLength'))
        return
      }
      let params = {
        CmpId: this.details.CmpId,
        UserName: this.details.UserName,
        Mobile: this.details.Mobile,
        Code: this.details.Code,
        NewPwd: hex_sha1(this.NewPwd),
        IsSign: true
      }
      kpiUserResetPassword(params).then((res) => {
        console.log('忘记密码的重置密码: ', res)
        if (res.ResultData === true) {
          this.$toast(this.$t('TM.ChangeSucceed'))
          this.$router.push('/login')
        }
      })
    }
  },
  components: {
    CHeader,
    HsGradientButton,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.title
    width 100%
    padding 0 10px
    >h1
      font-size 1rem
      font-weight bold
      margin-top 10px
    >p
      font-size 0.9rem
      margin-top 5px
      color $color_black_text_1
  >.information
    width calc(100% - 20px)
    margin 20px auto 0 auto
    background-color $color_white
    border-radius 4px
    .custom-cell
      @extend .flex-row
      @extend .flex-start
      width 100%
      height 50px
      >.logo
        width 22px
        height 22px
        background-size 100% 100%
        margin-right 10px
      >input
        width calc(100% - 40px)
        height 40px
</style>



